import request from '@/utils/request.js'

// 获取学员列表
export const getGroups = () => request.get('/v1/user/findByClassId')

// 新增积分
export const addIntegral = data => request.post('/v1/user/addIntegral', data)

// 获取学员作业列表
export const getTaskList = data => request.post('/v1/work/findStudentWork', data)

// 提交作业
export const submitTask = (workId, status) => request.get('/v1/work/submitWork/' + workId + '/' + status)

// 导出学员信息
export const exportExcel = () => request.get('/v1/user/exportUser')

// 重置学生积分
export const reset = groupId => request.get('/v1/user/resetGScoreByGroupId/' + groupId)
