<template>
  <div class="home">
    <el-row :gutter="10">
      <el-col class="left" :span="19" v-loading="leftLoading">
        <ul class="groups">
          <li class="group_item" :class="{'group_active': i == groupActive}" v-for="(item, i) in list" :key="item.groupId">
            <el-badge
            type="primary">
              <div class="groupsName">
                <span>{{ item.groupName }}</span>
                : 共计
                <span>{{ item.groupsIntegral }}</span> 分 ：
              </div>
            </el-badge>
            <el-tag @click="setStudents(i, childI)" class="students_item" :class="{active: childI === active}" v-for="(childItem, childI) in item.children" :key="childItem.id" effect="dark">
              {{ childItem.name }}
            </el-tag>
          </li>
        </ul>
      </el-col>
      <el-col style="height: 100%" class="right" :span="5">
        <div class="import">
        </div>
        <el-divider content-position="left">上传班级信息</el-divider>
        <div class="upload-button">
          <!-- 导入班级账号信息 -->
          <el-upload
            class="upload-demo"
            action="http://32633x11x9.zicp.vip/v1/user/importUser"
            :headers="headersObj"
            :on-success="onClassNumberSuccess"
            multiple>
            <el-button style="width: 100%" type="primary">上传学员账号</el-button>
          </el-upload>
          <!-- 导入班级账号信息 -->
          <el-upload
            class="upload-demo"
            action="http://32633x11x9.zicp.vip/v1/group/importGroup"
            :headers="headersObj"
            :on-success="onClassGroupsSuccess"
            multiple>
            <el-button style="width: 100%" type="primary">上传班级小组</el-button>
          </el-upload>
          <el-button @click="exportStudentsExcel" style="width: 100%" type="warning">导出学员账号</el-button>
        </div>
        <el-divider content-position="left">抽选提问</el-divider>
        <el-button @click="selection" :disabled="isStart" style="width: 100%" type="primary">
          提问
        </el-button>
        <el-button
        @click="startRush"
        style="background: #C00000;margin: 10px 0; width: 100%" :disabled="isRush" type="danger">
          {{ isRush ? '抢答中' : '开始抢答' }}
        </el-button>
        <el-input-number style="margin: 10px 0px; width: 100%;" v-model="number"></el-input-number>
        <el-button style="width: 100%" @click="confirmIntegral(number)" type="primary">
          确认积分
        </el-button>
        <el-button @click="reset" class="reset-btn" type="warning">重置班级小组积分</el-button>
        <el-divider content-position="left">快速加减积分</el-divider>
        <div class="addNum">
          <el-button @click="confirmIntegral(2)" size="mini">+2</el-button>
          <el-button @click="confirmIntegral(5)" size="mini">+5</el-button>
          <el-button @click="confirmIntegral(10)" size="mini">+10</el-button>
          <el-button @click="confirmIntegral(20)" size="mini">+20</el-button>
        </div>
        <div class="subNum">
          <el-button @click="confirmIntegral(-2)" size="mini">- 2</el-button>
          <el-button @click="confirmIntegral(-5)" size="mini">- 5</el-button>
          <el-button @click="confirmIntegral(-10)" size="mini">- 10</el-button>
          <el-button @click="confirmIntegral(-20)" size="mini">- 20</el-button>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { getGroups, addIntegral, reset } from '../api/students.js'
import { addRush, getRushStudents } from '../api/rush.js'
// import UploadExcel from '@/components/upload-excel.vue'
export default {
  name: 'Home',
  data () {
    return {
      list: [], // 班级组员
      groupActive: 0, // 选中小组
      active: 0, // 选中的组员
      setId: null, // 定时器id
      number: 1, // 积分选择框
      leftLoading: false,
      isStart: false,
      isRush: false,
      headersObj: {
        token: localStorage.getItem('hmhz-intergral-token')
      }
    }
  },
  created () {
    this.getGroups()
  },
  methods: {
    onClassNumberSuccess (res) {
      if (res.code === 0) {
        this.$message.success('导入成功')
      }
    },
    onClassGroupsSuccess (res) {
      if (res.code === 0) {
        this.$message.success('导入成功')
        this.getGroups()
      }
    },
    // 获取班级学员
    async getGroups () {
      this.leftLoading = true
      const { data: res } = await getGroups(this.$store.state.classId)
      if (res.code !== 0) return this.$message('获取失败')
      // 增加测试数据
      // for (let i = 0; i < 9; i++) {
      //   const arr = []
      //   for (let j = 0; j < 4; j++) {
      //     arr.push({
      //       ...res.data[0],
      //       groupId: i,
      //       id: i * j + 1,
      //       name: res.data[0].name + j
      //     })
      //   }
      //   res.data.push(...arr)
      // }
      // 数据格式化
      const groups = []
      res.data.forEach((item, i) => {
        const findRes = groups.find(groupsItem => groupsItem.groupId === item.groupId)
        if (findRes) {
          if (!findRes.children) {
            findRes.children = [item]
          } else {
            findRes.children.push(item)
          }
        } else {
          groups.push({
            ...item,
            children: [
              item
            ]
          })
        }
      })
      // 保存数据
      this.leftLoading = false
      this.list = groups
    },
    // 点击提问
    selection () {
      let selectNum = 0
      // 随机抽取组
      this.setId = setInterval(() => {
        selectNum++
        if (selectNum === 50) return clearInterval(this.setId)
        const group = Math.floor(Math.random() * this.list.length)
        const students = Math.floor(Math.random() * this.list[group].children.length)
        this.groupActive = group
        this.active = students
      }, 50)
    },
    // 确认积分
    async confirmIntegral (integral) {
      const { id, name } = this.list[this.groupActive].children[this.active]
      const { data: res } = await addIntegral({
        id,
        integral
      })
      this.list[this.groupActive].groupsIntegral += integral
      this.list[this.groupActive].children[this.active].integral += integral
      if (res.code !== 0) return this.$message('添加失败')
      if (integral > 0) return this.$message.success('恭喜🎉🎉🎉🎉🎉🎉' + name + '同学，回答正确！为第' + (this.groupActive + 1) + '组赢得' + integral + '积分')
      this.$message.error('同学，要认真观看视频了呦！😫😫😫😫，争取下次赢得积分，本次扣除第' + (this.groupActive + 1) + '组' + Math.abs(integral) + '积分')
    },
    // 手动选择
    setStudents (i, childI) {
      this.groupActive = i
      this.active = childI
    },
    // 开始抢答
    async startRush () {
      this.isRush = true
      const { data: res } = await addRush()
      if (res.code === 0) {
        let i = 0
        const setId = setInterval(async () => {
          i++
          if (i > 4) {
            clearInterval(setId)
            this.isRush = false
            this.$message('没有同学抢答呦')
            return
          }
          const { data: res } = await getRushStudents()
          // 有同学抢答到了
          if (res.code !== 400007 && res.data !== '' && res.data !== null) {
            clearInterval(setId)
            this.isRush = false
            this.setRushStudents(res)
          }
        }, 1000)
      }
    },
    // 找到抢答同学
    setRushStudents (res) {
      console.log(res.data)
      console.log(this.list)
      let findStudents = null
      this.list.find((listItem, i) => {
        const listItemRes = listItem.children.find((childItem, i2) => {
          if (childItem.id === (res.data - 0)) {
            findStudents = childItem
            this.active = i2
            return true
          } else {
            return false
          }
        })

        if (listItemRes) {
          this.groupActive = i
          return true
        } else {
          return false
        }
      })

      // console.log(findStudents)

      this.$message.success('恭喜' + findStudents.name + '抢答成功')
    },
    exportStudentsExcel () {
      window.open('http://32633x11x9.zicp.vip/v1/user/exportUser/' + this.list[0].classId)
    },
    async reset () {
      await reset(this.list[0].classId)
      this.$message.success('积分已重置')
      this.getGroups()
    }
  }
}
</script>

